export default {
    data() {
        return {
            name: '',
			weixin: '',
			qq: '',
            mobile: '',
			checked: false,
			is_agree: 'off'
        }
    },
    methods: {
		// 是否同意协议
		agreeAgreement(){
			
			if(this.checked){
				this.checked = false;
				this.is_agree = 'off';
			}else{
				this.checked = true;
				this.is_agree = 'on';
			}
			// console.log(this.checked)
		},
		// 信息验证
		checkData (data) {
			if (!data.name) {
				this.$common.errorToShow('请输入您的姓名')
				return false
			} else if (!data.weixin) {
				this.$common.errorToShow('请输入您的微信')
				return false
			} else if (!data.qq) {
				// console.log(this.is_agree)
				this.$common.errorToShow('请输入您的QQ')
				return false
			} else if (!data.mobile) {
				this.$common.errorToShow('请输入您的手机号')
				return false
			} else if (data.mobile.length !== 11) {
				this.$common.errorToShow('手机号格式不正确')
				return false
			} else if (data.agreement != 'on') {
				//console.log(data)
				this.$common.errorToShow('请钩选分销协议')
				return false
			} else {
				return true
			}
		},
		// 提交审核
		goApplyState() {
			let data = {
				name: this.name,
				weixin: this.weixin,
				qq: this.qq,
				mobile: this.mobile,
				agreement: this.is_agree,
			}
			
			if (this.checkData(data)) {
				this.$api.applyDistribution(data, res => {
					if(res.status){
						this.$common.successToShow(res.msg, function(){
							uni.navigateTo({
								url:'./apply_state'
							});
						});
					}else{
						this.$common.errorToShow(res.msg);
					}
				});
			}
		},
		goAgreement(){
			uni.navigateTo({
				url: './agreement'
			})
		}
        
    }
}
